import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, TipoMovimentoInterno } from '../../../../models/dtos/mlf/tipoMovimentoInterno';
import { TIPO_MOVIMENTO_INTERNO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoMovimentoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface TipoMovimentacaoInternaEditViewProps extends EditViewProps<TipoMovimentoInterno> {}

const TipoMovimentacaoInternaEditView: FC<TipoMovimentacaoInternaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const { reset, ...formProps } = useForm<TipoMovimentoInterno>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      const { grupoTipoMovimentacaoInterna } = globalReducer.globalParameter.grupoEmpresaConfig;
      setTimeout(() => {
        reset({
          ...selectedItem,
          grupoTipoMovimentacaoInterna: {
            id: grupoTipoMovimentacaoInterna.id,
          },
        });
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Tipo Movimentação Interna de Lançamento" wikiId={2467}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="grupoTipoMovimentacaoInterna.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="tipoStr"
              label="Tipo"
              isPrependLabel={true}
              placeholder="Informe o Tipo de Movimentação Interna"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: TIPO_MOVIMENTO_INTERNO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default TipoMovimentacaoInternaEditView;
